.walk-info-dog {
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;

  &__title {
    font-weight: 700;
    font-size: 30px;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    text-align: center;
    margin: 20px 0;
  }

  &__add-button {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 5px;
    height: 60px;
    width: 60px;
    border-radius: 50%;
    background-color: #8fe388;
  }

  &__empty-slot {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 5px;
    height: 60px;
    width: 60px;
    border-radius: 50%;
    background-color: #c4c4c4;
    border: 0;
  }

  &__content {
    flex: 1;
    width: 80%;
  }

  &__subtitle {
    font-size: 1.8rem;
    font-weight: 600;
    margin-right: 5px;
  }

  &__avatar {
    margin-right: 5px;
    height: 60px;
    width: 60px;
    border-radius: 50%;
  }

  &__menu-avatar {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    margin-right: 5px;
  }
}
