.empty-list {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__dog {
    height: 300px;
    object-fit: contain;
  }

  &__text {
    font-weight: 700;
    font-size: 30px !important;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    text-align: center;
  }
}
