.dog-profile {
  height: calc(100vh - 55px);
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;

  @media only screen and (max-height: 550px) {
    height: 100vh;
  }

  &__image {
    margin: 20px 0;
    height: 220px;
    width: 220px;
    border: 1px solid #a7ecae;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    border-radius: 114px;
  }

  &__name {
    font-weight: 500;
    font-size: 36px;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }

  &__section {
    margin: 10px 0;
    width: 100%;
  }

  &__section-title {
    margin-bottom: 10px;
    display: flex;
    align-items: center;
  }

  &__line {
    height: 1px;
    background-color: black;
    flex: 1;
    margin: 0 10px;
  }

  &__section-infos {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  &__section-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50px;
    text-align: center;
  }

  &__section-info-title {
    font-weight: 700;
  }

  &__buttons {
    flex: 1;
    margin-bottom: 40px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
  }

  &__owner-info {
    display: flex;
    align-items: center;
  }

  &__owner-image {
    margin-right: 5px;
    height: 40px;
    width: 40px;
    border: 1px solid #a7ecae;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    border-radius: 114px;
  }
}
